import { useStore } from 'vuex';
import { showFailToast } from 'vant';
export default {
  props: ['title', 'isEdit'],
  setup() {
    let store = useStore();
    const changeEdit = () => {
      if (store.state.cartList.length === 0) {
        // 提示
        showFailToast('请先添加商品到购物车');
      } else {
        // 底部 结算和删除的切换
        store.commit('changeCartFooter');
        // 点击 编辑 展示 完成
        store.commit('changeHandleEdit');
      }
    };
    return {
      changeEdit,
      store
    };
  }
};